.frame-child {
  position: absolute;
  top: 0;
  left: 0;
  width: 1440px;
  height: 900px;
  object-fit: cover;
}
.day-money-back-guarantee {
  position: absolute;
  top: 8px;
  left: calc(50% - 83.5px);
  letter-spacing: 0.01em;
  line-height: 91.02%;
}
.icon-calendar {
  position: absolute;
  top: 0;
  left: calc(50% - 113.5px);
  width: 25px;
  height: 25px;
  overflow: hidden;
}
.day-money-back-guarantee-parent,
.get-puremax {
  position: absolute;
  top: 72px;
  left: 19px;
  width: 227px;
  height: 25px;
}
.get-puremax {
  margin: 0;
  height: 81.25%;
  width: 87.74%;
  top: 18.75%;
  left: 0;
  line-height: 100%;
  display: flex;
  align-items: center;
}
.arrow-right-icon {
  position: absolute;
  height: 100%;
  width: 12.26%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 87.74%;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
}
.get-puremax-parent {
  position: absolute;
  top: 22px;
  left: 52px;
  width: 159.56px;
  height: 16px;
}
.cta-button {
  position: absolute;
  top: 0;
  left: calc(50% - 132px);
  border-radius: 30px;
  background-color: #ffa124;
  width: 264px;
  height: 60px;
  font-size: 22px;
  color: #001219;
}
.frame-container {
  position: absolute;
  top: 216px;
  left: 1.82px;
  width: 264px;
  height: 97px;
  cursor: pointer;
}
.plan-with {
  color: #fff;
}
.year-plan-with-container {
  margin: 0;
  position: absolute;
  top: 68px;
  left: 0.82px;
  font-size: 32px;
  letter-spacing: -0.02em;
  color: #ffba24;
}
.months-extra {
  margin: 0;
  position: absolute;
  top: 103px;
  left: 1.82px;
  font-size: 42px;
  letter-spacing: -0.01em;
}
.beatthisoffer {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 46.82px;
  letter-spacing: -0.02em;
}
.a-child,
.frame-group {
  position: relative;
  width: 345px;
  height: 313px;
}
.a-child {
  width: 734px;
  height: auto;
  object-fit: cover;
  overflow-x: hidden;
  overflow-y: hidden;
  max-width: 650px;
}
.a {
  position: absolute;
  top: 352px;
  left: 165px;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: center;
  gap: 31px;
  text-align: left;
  font-size: 14px;
  color: #fff;
  font-family: Roboto;
}
.desktop,
.frame-parent {
  height: 900px;
  overflow: hidden;
}
.frame-parent {
  position: absolute;
  top: 0;
  left: 0;
  background-color: #001219;
  width: 1440px;
}
.desktop {
  position: relative;
  width: 100%;
  background-image: url(./public/desktop@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  overflow-x: hidden;
  overflow-y: hidden;
}
@media screen and (max-width: 1200px) {
  .a-child {
    max-width: 400px;
    height: auto;
    margin: auto;
  }
}
@media screen and (max-width: 960px) {
  .frame-group {
    width: 345px;
    height: 313px;
    margin-left: 0;
  }
  .a-child {
    max-width: 350px;
    height: auto;
    margin: auto;
  }
  .a {
    left: 130px;
    top: 190px;
  }
  .frame-parent {
    height: auto;
  }
}
@media screen and (max-width: 420px) {
  .cta-button:hover {
    background: linear-gradient(#fff, #fff), #ffa124;
  }
  .a-child {
    max-width: 300px;
    height: auto;
  }
  .a {
    flex-direction: column;
    left: 30px;
    top: 130px;
  }
}
@media screen and (max-width: 768px) {
  .a {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: 50px;
  }
}
@media screen and (max-width: 1441px) {
  .frame-parent {
    width: 100%;
    height: 100%;
  }
}
@media screen and (max-width: 1024px) {
  .a {
    left: 130px;
  }
}
